.grid {
  display: grid;
  grid-template-columns: repeat(12,1fr);
  column-gap: 20px;

  @include Phone {
    grid-template-columns: repeat(2,1fr);
    column-gap: 10px;
  }

  @include Tablet {
    grid-template-columns: repeat(6,1fr);
    column-gap: 20px;
  }

  @include Full {
    max-width: 1440px;
    margin: auto;
  }

  &__item {
    @for $start from 1 through 12 {
      @for $end from $start through 12 {
        &--#{$start}-#{$end} {
          grid-column: #{$start} / #{$end+1};
        }
      }
    }
  }
}
